import StartPage from "../page/StartGame";
import GamePage from "../page/GamePage";
import EndPage from "../page/EndPage";



const map={
    "GamePage":GamePage,
    "EndPage":EndPage,
    "StartPage":StartPage,
}

export const getPage=(page)=>{
    if(map.has(page)){
        return map.get(page)
    }else{
        return map.get("StartPage")
    }
}

